* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --primary: rgba(68, 93, 143, 0.560);
  /* --primary: rgba(70, 93, 143, 0.600); very good */
  --secondary: rgba(164, 200, 245, 0.900);
  --tertiary:  rgb(124, 151, 219);
  --quatrinary:  rgb(90, 103, 173);
  --emphasis-list-item-color: rgba(88, 113, 163, 0.960);
  --super-light  : rgba(174, 210, 255, 0.900);
  --light: rgb(110, 127, 187);
  --heading-prime_color: rgba(61, 83, 143, 0.980);
  /* --light: rgb(96, 113, 173); */
  /* --light: rgba(222, 208, 184, 0.221); */
  --dark: rgba(66, 83, 143, 0.970);
  /* --dark: rgba(121, 81, 76, 0.956); */
  --white: rgb(255, 255, 255);
  --petal-theme-color: rgb(219, 112, 121);
  --emphasis-catchy: rgba(70, 83, 143, 0.980);
  --err-msg: pink;

 --border-radius: 0.31rem;
 --spacing-one: calc(1rem * 0.25);
 --spacing-two: calc(1rem * 0.5);
 --spacing-three: 1rem;
 --spacing-four: calc(1rem * 1.5);
 --spacing-five: calc(1rem * 3);
 --designer-font-primary: "Aladin"; /*"Playball";*/
 --casual-technical-font: "Aladin";
 --paragraph-coding-mono-font: "JetBrains Mono"; /*"Marmelad"; */
 --title-flower-theme-font: "Marmelad";

}

/* We want the custom properties to be global, meaning that
any element or class selector can use them, so we use :root pseudo-class */

html,
body {
  min-height: 100%;
   
  margin: 0;
  padding: 0;

  font-family: 'Playball' ;
  font-weight: 400;
  line-height: 1.5;
  font-size: 20px;
  color: var(--white);
  opacity: 0.98;

  background-color: var(--primary);
  background: linear-gradient(var(--emphasis-list-item-color), var(--primary));
  background: -webkit-linear-gradient(var(--emphasis-list-item-color), var(--primary)); 
}

header{
  border-bottom: 1px solid var(--emphasis-list-item-color);
  position: sticky;
  top: 0;  /* Keep this header element on top of the stack, aka to the front */
  z-index: 9999;  /* To avoid future conflicts although the number seems like overkill - per lesson note */ 
  background-color: var(--super-light ) !important;
  opacity: 0.90;
  padding-top: 1px;
  height: 121px;
}

header h1{
   color: var(--heading-prime_color);
   font-family: var(--designer-font-primary);
   font-weight: 700;
   text-shadow: 1px 1px var(--white) !important;
}
 
 .hero {
  /* background-image: url("assets/images/hero-bg-tulips.jpg"); */
  /*background-size: cover;  tells the browser to make sure the image always covers the entire container, image might not be whole*/
  /*background-position: center;  affect the bg image position*/    
  /*background-attachment: fixed;  places the image relative to the viewport instead of the element. */
  /* background-position: 100%; use same percentage to make sure the hero and the header nav pictures line up, hero bg graphics not in use */
  
  display: flex;
  justify-content: right;
  flex-wrap: wrap;
  align-items: flex-end; /*in case h3 doesn't get pushed down and there are 2 or more content entities*/
  border-top: 3px dotted var(--tertiary);
  border-bottom: 3px dotted var(--tertiary);
  background-color: transparent;
  margin-top: 0;
  padding-bottom: 12px;
 
  /* background: linear-gradient(var(--quatrinary), var(--white));
  background: -webkit-linear-gradient(var(--quatrinary), var(--white));  */
 
 }
 
 .hero-greeting {   
  text-align: center;
  margin: 4.0% 0 0 1.5%;
  /* margin: 0; */
  font-size: 15px;
 }
 
 .hero-greeting h4 {
  font-style: normal;
  font-size: 44px;
  font-family: 'Courier New', Courier, monospace;
  color: var(--secondary); 
  
  /* padding: 35px 15px; */
  padding: 10px 15px;
  text-shadow: -2px 2px 1px var(--tertiary);
  margin: 35px 180px 35px 0;
 }
 
 /* End of Hero Section*/


h1,
h2,
h3,
h5{
  font-family: var(--designer-font-primary); 
  font-weight: 700;
  margin: 5px 0;
  line-height: 1.25;
}
 
h4{
  font-family: var(--designer-font-primary); 
  font-weight: 600;
  margin: 0;
  line-height: 1.25;
  
}

 
h5, 
h6 {
  font-family: var(--designer-font-primary); 
  font-weight: 600;
  margin: 5px 0;
  line-height: 1.25;
  color: var(--emphasis-catchy);
}

h1 {
  font-size: 2.60rem;
  letter-spacing: 1px !important;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.4rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
