nav {
  /* width: 81%;  */
  /* would effect mobile rendering*/
  justify-content: center;
  padding: 19px 10px 20px 0;
  
}

nav a, span {
  padding: 1px 1px 5px 0;
  margin: 0 4px 0 0;
  transition: all 0.2s;

  color: var(--dark) !important;
  text-shadow: 1px 1px var(--primary) !important;
  font-size: 1.4rem; 
  font-family: var(--casual-technical-font) !important;
  font-weight: 700;
  letter-spacing: 1px;

  /* border-bottom: 1px solid var(--super-light);  */
  
}

nav a:hover, span:hover{
  color: var(--white)!important;
  text-shadow: 1px 1px 1px var(--emphasis-list-item-color) !important;
  font-size: 1.4rem !important;
  font-family: var(--casual-technical-font) !important;
  font-weight: 700;
  letter-spacing: 1px;
  
}

/* for the menu only */
.exclusive span {
  padding: 1px 31px 0 10px;
  margin: 0 4px 0 0;
  transition: all 0.2s;

  color: var(--dark) !important;
  text-shadow: 1px 1px var(--primary) !important;
  font-size: 1.4rem !important; 
  font-family: var(--casual-technical-font) !important;
  font-weight: 700;
  letter-spacing: 1px;  
}

.exclusive span:hover {
  color: var(--white)!important;
  text-shadow: 1px 1px 1px var(--emphasis-list-item-color) !important;
  font-size: 1.4rem !important;
  font-family: var(--casual-technical-font) !important;
  font-weight: 700;
  letter-spacing: 1px;
}

.exclusive .navActive { 
  cursor: auto;
  
  font-family: var(--casual-technical-font) !important;
  font-weight: 700;

  color: var(--white) !important;
  text-shadow: 1px 1px 1px var(--emphasis-list-item-color) !important;
  
}

main .profile-photo {
  /* width: 157px ; 
  height: 220px;  */
  /* width: 142px ; 
  height: 219px ;  */
  width: 126px  ; 
  height: 194px ;
  opacity: 98% !important;
}

main .about-me {
  font-size: 0.68rem !important;
  font-weight: 400;
}

main p{  
  font-weight: 500;
  letter-spacing: 0 !important;
  font-size: 0.82rem !important;
  font-family: var(--paragraph-coding-mono-font);
}


main a {
  color: var(--dark);
  font-size: 1.1rem !important;
  text-decoration: none;
}

main a:hover{
  color: var(--white);
  font-size: 1.1rem !important;
  text-shadow: 1px 1px 1px var(--emphasis-list-item-color) !important;
  text-decoration: none;
  font-weight: 500;
}

main h2{  
  text-shadow: 1px 1px 1px var(--white) !important;
  letter-spacing: 2px !important;
  margin: 0 0 18px 0;  
}

main h4{
  font-size: 1.38rem !important;
  color: var(--primary);
  text-shadow: 1px 1px 1px var(--white) !important;
  margin-bottom: 8px;
  letter-spacing: 1px;
}

.separator {
  border-bottom: 3px solid var(--super-light); 
  border-radius: 5px;
  
}

.pill {
  margin: 5px 0;
  padding: 0.07rem 0.45rem;

  outline: none;
  border-radius: 3px;
  border:  1px solid var(--dark) !important;
  
  color: var(--dark);
  background-color: var(--secondary);

  text-decoration: none;
  transition: all 0.5s;  

} 

.btn{
  margin: 0.87rem 0;
  padding: 0.37rem 0.85rem;
  width: 35%;

  outline: none;
  border-radius: var(--border-radius);
  border:  1px solid var(--super-light) !important;

  color: var(--secondary);
  background-color: var(--dark);

  text-decoration: none;
  transition: all 0.5s;  

  font-family: var(--paragraph-coding-mono-font);
  font-size: 1.0rem;
  font-weight: 500;

} 

.btn:hover
{
  /* color: var(--secondary);
  background-color: var(--dark); */
  border:  1px solid var(--super-light) !important;

  color: var(--dark);
  background-color: var(--secondary);

  font-family: var(--paragraph-coding-mono-font);
  font-size: 1.0rem;
  font-weight: 500;
 
}
 
.pill:hover {
  color: var(--white);
  background-color: var(--tertiary);
}

.btn-inline {
  display: inline;
}

.border-top-thin{
  border-top: 1px solid var(--emphasis-list-item-color);
}

.border-photo-thin{
  border: 1px solid var(--super-light);
}

/* FORM */
.form-label,
.form-input,
.form-textarea {
  display: block;
  font-family: var(--paragraph-coding-mono-font);
}

.form-label {
  font-size: 0.95rem;
  /* margin: 0.925rem 0 0.425rem 0.25rem; */
  margin: 0.97rem 0 0.32rem 0 !important;

}

.form-error{
  color: var(--err-msg);
  font-size: 0.81rem !important;
  
  padding-top: 0.256rem;
  border-top: 2px solid var(--err-msg) !important;
  border-radius: 3px;
}

.form-textarea {
  width: 100%;
  height: 150px;

  padding: 0.55rem;
  margin: 6px 100px 10px 0 !important;
 
  border: 1px solid var(--quatrinary);
  border-radius: var(--border-radius);

  color: var(--emphasis-list-item-color);
  font-family: var(--title-flower-theme-font);
  font-size: 1.0rem;
  font-weight: 500;
} 

.form-input {
  width: 100%;

  padding: 0.75rem;
  margin: 0.2rem 30px 0.2rem 0;

  border: 1px solid var(--quatrinary);
  border-radius: var(--border-radius);

  color: var(--emphasis-list-item-color);
  font-family: var(--title-flower-theme-font);
  font-size: 1.0rem;
  font-weight: 500;
 
} 
 
textarea:focus, input:focus{
  outline: none;
  font-weight: 300;
  color: var(--emphasis-list-item-color);
}

textarea, input{
  color: var(--emphasis-list-item-color);
}


