/********** Start of Graphic Hovering based Portfolio: position should not depend on these content definitions *************/
/********** Look for worksample-photo and worksample-detail instead - only font-size matters here ***********/

#hover-content {
  display: none;
  transition: all 0.6s;
   
}

#hover-me:hover #hover-content {
  display:block;
  transition: all 0.6s;  
}

/* varies per media query - start here */
#hover-me:hover #hover-content a {
  transition: all 0.6s;

  color: var(--dark);
  text-shadow: -1px -2px 2px var(--white);

  font-family: var(--title-flower-theme-font) !important;
  font-weight: 700;

  font-size: 0.76rem !important;
  padding-right: 5px;
}

#hover-me:hover #hover-content a:hover {
  /* color: var(--white);
  text-shadow: -2px -3px 1px var(--petal-theme-color)  !important; */

  color: var(--petal-theme-color);
  text-shadow: -2px -2px 1px var(--white)  !important;

  font-family: var(--title-flower-theme-font);
  font-weight: 800;

  font-size: 0.76rem !important;
}

#hover-me:hover #hover-content img {
  width: 10%;
  height: 32%;
  /* margin-left: 13px; */
  margin-left: 2px;
  padding-top: 15px;
  opacity: 0.99;  
}

/* To dim the github icon when hovering */
#hover-me:hover #hover-content img:hover {
  opacity: 0.85;  
}

#hover-me:hover #be-gone {
  display: none;
  transition: all 0.6s;
}

#hover-me:hover {
  opacity: 0.95;
  transition: all 0.6s;
}

